@import 'variables';



.main {
    &__products {
        display: flex;
        justify-content: center;
        margin: 70px 0px;
        &__template {
            max-width: calc( 100vw - 400px );
            min-width: calc( 100vw - 400px);
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            // grid-template-rows: repeat(10, 400px)
            justify-items: center;
            &__item {
                max-width: 350px;
                max-height: 370px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                margin-bottom: 30px;
                // grid-column: 1 / 4
                // grid-row: 1 / 10
                &__img {
                    width: 100%;
                    height: 100%;
                    object-fit: contain; }
                &__name {
                    font-family: 'main';
                    font-size: 20px;
                    font-weight: 300;
                    color: rgba(0, 0, 0, 0.70); }
                &__button {
                    @include button; } } } } }

